.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.titleBar {
  background: #000;
  display: block;
  color: #fff;
  padding-left: 16px
}

.searchBar {
  font-size: 24px;
  display: block;
  width: 100%;
  padding: 8px 0;
}